import uniNavBar from '@/components/uni-nav-bar/uni-nav-bar.vue';
import uniSteps from '@/components/uni-steps/uni-steps.vue';
import uniList from '@/components/uni-list/uni-list.vue';
import uniListItem from '@/components/uni-list-item/uni-list-item.vue';
import uniPopup from '@/components/uni-popup/uni-popup.vue';
import json from '@/json';

export default {
	components: {
		uniNavBar,
		uniSteps,
		uniList,
		uniListItem,
		uniPopup
	},
	data() {
		return {
			id:"",
			showtip1: 'center',
			detailData: {},
			imgList: [],
			active: 1,

			userFeedbackHidden: true, // 默认隐藏
			feedbackContent: '', // 用户反馈内容
			latitude: 30.663429,
			longitude: 104.072422,
			covers: [
				{
					latitude: 30.663429,
					longitude: 104.072422,
					iconPath: '../../static/location.png',
					width: 50,
					height: 50
				}
			]
		};
	},
	onLoad(options) {
		var detailData = JSON.parse(options.data);
		this.id=detailData.id;
		console.log(this.id)
		// this.detailData = json.detail.data;
		this.loadNewsList();
	},
	methods: {
		//获取推荐列表
		async loadNewsList() {
			this.detailData = json.detail.data;
			console.log(this.detailData);
			this.$http
				.get('/interface/rest/http/xlwb/xlgc-wb-xcx-sjzx-sjxq.htm', {
					params: {
						id: this.id
					}
				})
				.then(res => {
					console.log(res);
					if (res.statusCode == 200) {
						var list = res.data.list;
						console.log('list');
						console.log(list);
					} else {
						uni.showLoading({
							title: '请求失败'
						});
					}
				})
				.catch(err => {
					console.log(err);
				});
		},
		statusChange(index) {
			switch (index) {
				case 1:
					return '待处理';
					break;
				case 2:
					return '处理中';
					break;

				case 3:
					return '已处理';
					break;
				default:
					return '待处理';
			}
		},
		back() {
			uni.navigateBack({
				delta: 1
			});
		},
		scroll(e) {
			console.log(e.detail.scrollTop);
		},
		//图片预览
		previewImage(e) {
			var current = e.target.dataset.src;
			console.log('current');
			console.log(current);
			uni.previewImage({
				current: current,
				urls: this.detailData.imgList
			});
		},
		submitMsg() {
			console.log('確認完成');
			this.$nextTick(() => {
				this.$refs.showtip.open();
			});
		},
		alertMsg() {
			console.log('我要反馈');
			this.$nextTick(() => {
				this.$refs.showtip1.open();
			});
		},

		submitFeedbackContent() {
			this.detailData.feedbackContent = this.feedbackContent;
			//发起请求
			this.$refs.showtip1.close();
		},
		submitOver(){
				this.$refs.showtip.close();
			this.$http
				.get('/interface/rest/http/xlwb/xlgc-wb-xcx-sjzx-clwc.htm', {
					params: {
						
					}
				})
				.then(res => {
					console.log(res);
					if (res.statusCode == 200) {
						uni.showToast({
						    title: res.data.msg,
						    duration: 2000
						});
						setTimeout(() => {
							this.back()
						}, 1000);
						 
					} else {
						uni.showLoading({
							title: '请求失败'
						});
					}
				})
				.catch(err => {
					console.log(err);
				});
		},
		cancel(type) {
			this.$refs.showtip.close();
			this.$refs.showtip1.close();
		}
	}
};
